<template>
	<div class="newWallet">
		<b-alert variant="info" show>{{msg}} ---> Location: {{location}}</b-alert><br>
		<b-alert ></b-alert>
		<b-alert variant="success" :show="showBackendResponse">Backend response:{{response}}</b-alert>
		<!--<b-alert variant="danger" :show="showBackendErrorHeader">Backend response Error:{{errors.header.data}}</b-alert>-->
		<b-alert variant="danger" :show="showBackendError">Backend response Detail Error:{{errors}}</b-alert>
		<button @click="callRestService">Call Api </button>

	</div>
</template>

<script>
	// import axios from 'axios'
	import {AXIOS} from './http-common.js'
	export default {
	  name: 'NewWallet',
	  data () {
	    return {
	      msg: 'Welcome to NewWallet',
	      showBackendResponse: false,
	      showBackendError: false,
	      showBackendErrorHeader: false,
	      location: 'D:\\WorkingFolder\\workspace\\monager\\frontend\\src\\components\\NewWallet.vue',
	      response:[] ,
          errors: [/*
          	{detail:''},
          	{header: ''}*/
         	] 
	    }
	  },
	  methods:{
	  	callRestService(){
	  		AXIOS.get('/hello')
	  		.then(response=>{
	  			this.response=response.data
	  			this.showBackendResponse= true;
	  			console.log(response.data)
	  		})
	  		.catch(e=>{
	  			//console.log(e.response)
	  			//this.errors.detail=e;
	  			//this.errors.header=e.response;
	  			this.showBackendError= true;
	  			this.showBackendErrorHeader= true;
	  			this.errors.push(e)
	  			
	  		})
	  	}	  	
	  }
	}	
</script>


