<template>
  <div id="app">
    <div>
      <b-navbar class="navbar fixed-top navbar-expand-lg navbar-dark bg-dark fixed-top">
          <b-navbar-brand href="/"><span class="border-bottom border-primary">{{appName}}</span></b-navbar-brand>
        <div class="container">
        <div class="collapse navbar-collapse" id="navbarNav">
          <ul class="navbar-nav ml-auto">
            <li class="nav-item" v-for="option in options" v-bind:key="option.text">
              <a class="nav-link " v-bind:href="option.href">{{option.text}}<span class="sr-only"></span></a>
            </li>
          </ul>
        </div>
      </div>  
      </b-navbar>
    </div>
    <router-view></router-view>
  </div>
</template>
<script>
export default {
  name: 'App',
  data: function (){
    console.log("reached App Page DOM");
    return {
      appName: 'monager',
      options: [
      {
        href: '/about',
        text: 'About'
      },
      {
        href: "/Contact",
        text: 'Contact'
      },
      {
        // href: '/#/newWallet', use this method when not using mode: 'history' in index.js is not used
        href: "/findallActivePools",
        text: 'ActivePools'
      },
      {
        href: '/createNew',
        text: 'CreatePool'
      },
      {
        href: '/login',
        text: 'Login'
      }
      ]
    }
  }
}
</script><!--
<style>
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
}
</style>
-->



